.button {
padding: 5px 24px;
    border-radius: 3px;
    border: none;
}

.disabled {
    background-color: #004D99;
    color: #002E5C;
}

.secondary {
    border: 1px solid #0066CC;
    color: #0066CC;
    background-color: #fff;
}

.default {
    background-color: #0066CC;
    color: #fff;
}

.red {
    background-color: #CC1439;
    color: #fff;
}

.default:hover {
    background-color: #0080FF;
}

.red:hover {
    background-color: #FF1A47;
}

.secondary:hover {
    background-color: #E5F0FA;
}


.default:active {
    background-color: #0059B2
}

.secondary:active {
    background-color: #C9E5FF;

}

.red:active {
    background-color: #B21232
}

.button:disabled {
    opacity: 0.5;
}
